@import url(https://fonts.googleapis.com/css2?family=Passion+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo {
  height: 100%;
  pointer-events: none;
}

.logo img {
  height: 100%;
  box-sizing: border-box;
  padding: 8px 16px;
}

.header {
  position: relative;
  z-index: 10;
  background-color: white;
  height: 96px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  box-sizing: border-box;
  padding: 8px 16px;
  box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.05), inset 0 -1px 0 rgba(0,0,0,0.15);
}

.footer {
  text-align: center;
  font-size: 0.75rem;
  color: #999;
}
.pagination-container {
  display: flex;
  justify-content: center;
}

.pagination-item {
  width: 2rem;
  height: 2rem;
  border: 1px solid #ff5500;
  color: #ff5500;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 0px 4px;
}

.pagination-item:hover {
  background-color: rgba(255,80,0,0.04);
}

.pagination-item.page-active {
  background-color: #ff5500;
  color: white;
}

.pagination-ellipsis {
  border: none;
  cursor: default;
}
.pk-type-wrapper {
  padding: 2px 16px;
  display: flex;
  justify-content: center;
}

.pk-type {
  display: flex;
  justify-content: center;
  align-items: center;
  background: lightgray;
  border: 1px solid;
  border-radius: 16px;
  min-height: 16px;
  box-sizing: content-box;
  width: 100%;
  font-size: 0.7rem;
  color: white;
  text-transform: uppercase;
}

.pk-type-null {
  visibility: hidden;
}

.pk-type-normal {
  background-color: #A8A878;
}
.pk-type-fighting {
  background-color: #C03028;
}
.pk-type-flying {
  background-color: #A890F0;
}
.pk-type-poison {
  background-color: #A040A0;
}
.pk-type-ground {
  background-color: #E0C068;
}
.pk-type-rock {
  background-color: #B8A038;
}
.pk-type-bug {
  background-color: #A8B820;
}
.pk-type-ghost {
  background-color: #705898;
}
.pk-type-steel {
  background-color: #B8B8D0;
}
.pk-type-fire {
  background-color: #F08030;
}
.pk-type-water {
  background-color: #6890F0;
}
.pk-type-grass {
  background-color: #78C850;
}
.pk-type-electric {
  background-color: #F8D030;
}
.pk-type-psychic {
  background-color: #F85888;
}
.pk-type-ice {
  background-color: #98D8D8;
}
.pk-type-dragon {
  background-color: #7038F8;
}
.pk-type-dark {
  background-color: #705848;
}
.pk-type-fairy {
  background-color: #EE99AC;
}
.pk-type-unknown {
  background-color: #68A090;
}
.pk-type-shadow {
  background-color: #292929;
}


.pk-card-wrapper {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 20%;
  max-width: 20%;
  box-sizing: border-box;
  padding: 16px;
}

.pk-card {
  padding: 20px 20px 16px;
  cursor: pointer;
  flex-direction: column;
  border-radius: 12px;
}

.pk-card:hover {
  border-color: #aaa;
  background-color: rgba(0,0,0,0.04);
}

.pk-card.pk-top {
  border-color: #ff5500;
  background-color: rgba(255,80,0,0.04);
}

.pk-top-star {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 8px;
  left: 8px;
}

.pk-top-star svg {
  flex: 0 0 24px;
  fill: #ff5500;
  width: 20px;
  height: 20px;
  transition: transform 100ms ease-out;
  animation-name: rocking;
  animation-duration: 1s;
  animation-timing-function: ease-in;
  animation-iteration-count: infinite;
}

.pk-card:hover .pk-top-star svg {
  animation-name: none;
  transform: scale(1.2) rotate(-10deg);
}

.pk-top-tooltip {
  position: absolute;
  line-height: 1rem;
  padding: 4px 8px;
  border-radius: 8px;
  border: 1px solid #ff5500;
  background-color: white;
  color: #ff5500;
  font-size: 1.2rem;
  font-family: 'Passion One', sans-serif;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,0.1), inset 0 -1px 0 0 rgba(0,0,0,0.15);
  opacity: 0;
  transform: scale(0.5);
  transition: opacity 100ms ease-out, transform 150ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.pk-top-star:hover .pk-top-tooltip {
  opacity: 1;
  transform: scale(1);
}

.pk-basic-info-container {
  display: flex;
  flex: 0 0;
  flex-direction: column;
}

.pk-name, .pk-name-stroke {
  text-transform: capitalize;
  position: absolute;
  top: -1.3rem;
  right: -0.5rem;
  font-size: 1.6rem;
  font-family: 'Passion One', sans-serif;
  color: white;
  transform-origin: right;
  -webkit-transition:
          -webkit-text-stroke-color 150ms ease-out 0ms,
          transform 100ms ease-out 0ms;
}

.pk-card:hover .pk-name, .pk-card:hover .pk-name-stroke  {
  transform: scale(1.05);
}

.pk-card:hover .pk-name-stroke {
  -webkit-text-stroke-color: #ff5500;
}

.pk-name-stroke {
  -webkit-text-stroke: 0.2em #b7b7b7;
}

.pk-top .pk-name-stroke {
  -webkit-text-stroke: 0.2em #ff5500;
}

.pk-name:before, .pk-name-stroke:before {
  content: "#";
  font-size: 0.7em;
}

.pk-section {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #ddd;
  padding: 16px 0;
}

.pk-section:first-of-type {
  padding-top: 0;
}

.pk-section:last-of-type {
  border-bottom: none;
  padding-bottom: 0;
}

.pk-sprite-wrapper {
  border-radius: 50%;
  border: 1px solid rgba(0,0,0,0.075);
  width: 96px;
  height: 96px;
  padding: 4px;
  box-sizing: content-box;
}

.pk-card:hover .pk-sprite-wrapper {
  border-color: transparent;
  background-color: rgba(0,0,0,0.075);
}

.pk-card.pk-top:hover .pk-sprite-wrapper {
  border-color: rgba(255,80,0,0.3);
  background-color: rgba(255,80,0,0.04);
}

.pk-sprite {
  transition: transform ease;
}

.pk-card:hover .pk-sprite {
  animation-name: wiggle;
  animation-duration: 170ms;
  animation-iteration-count: 2;
  animation-delay: 200ms;
}

.pk-types {
  margin-top: 8px;
}

.pk-stats-container {
  display: flex;
  flex: 1 0 150px;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 16px;
}

.pk-stat-wrapper {
    display: flex;
    align-items: center;
}

.pk-stat-icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.pk-stat-icon-wrapper svg {
  flex: 0 0 24px;
  fill: #ddd;
  width: 24px;
  height: 24px;
  transition: fill 150ms ease-out;
}

.pk-card:hover .pk-stat-icon-wrapper svg {
  fill: #ff5500;
}

.pk-stat-tooltip {
  position: absolute;
  z-index: 1;
  line-height: 1rem;
  padding: 4px 8px;
  border-radius: 8px;
  border: 1px solid #ff5500;
  background-color: white;
  color: #ff5500;
  font-size: 1.2rem;
  font-family: 'Passion One', sans-serif;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,0.1), inset 0 -1px 0 0 rgba(0,0,0,0.15);
  opacity: 0;
  transform: scale(0.5);
  transition: opacity 100ms ease-out, transform 150ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.pk-stat-wrapper:hover .pk-stat-tooltip {
  opacity: 1;
  transform: scale(1);
}

.pk-stat-bar-wrapper {
  position: relative;
  height: 10px;
  width: 100%;
  margin: 0 8px;
}

.pk-stat-bar {
  position: absolute;
  height: 100%;
  background-color: #ddd;
  border-radius: 8px;
}

.pk-stat-bar-empty {
  width: 100%;
}

.pk-stat-bar-fill {
  min-width: 8px;
  opacity: 0.5;
  transition:
          background-color 150ms ease-out,
          width 2000ms cubic-bezier(0.22, 0.61, 0.36, 1);
}

.loading-disable .pk-stat-bar-fill {
  transition: width 2000ms cubic-bezier(0.22, 0.61, 0.36, 1);
}

.pk-card:hover .pk-stat-bar-fill {
  opacity: 1;
}

.pk-stat-value-wrapper {
  width: 2.5em;
  font-size: 0.875rem;
  text-align: right;
  opacity: 0.5;
}

.pk-card:hover .pk-stat-value-wrapper {
  opacity: 1;
}

.pk-details-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-around;
  width: 100%;
}

.pk-detail {
  display: flex;
  flex: 1 0;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: sans-serif;
  line-height: 1.2em;
}

.pk-detail-key {
  color: #e69573;
  font-size: 0.9rem;
}

.pk-detail-value {
  color: #bbb;
  text-transform: capitalize;
  white-space: nowrap;
  font-size: 0.8rem;
}

.pk-card:hover .pk-detail-key {
  color: #ff5500;
}

.pk-card:hover .pk-detail-value {
  color: #666;
}

@keyframes wiggle {
  0% {
    transform: translateY(0px);
  }
  25% {
    transform: translateY(1px);
  }
  75% {
    transform: translateY(-3px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes rocking {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(20deg);
  }
  75% {
    transform: rotate(-20deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.pokedex {
  padding: 32px;
}

.pk-cards {
  display: flex;
  flex-wrap: wrap;
  transition: opacity 100ms ease-out, -webkit-filter 100ms ease-out;
  transition: filter 100ms ease-out, opacity 100ms ease-out;
  transition: filter 100ms ease-out, opacity 100ms ease-out, -webkit-filter 100ms ease-out;
}

.pk-cards.loading-disable {
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
  opacity: 0.75;
  pointer-events: none;
}

